import * as React from "react";
import type { HeadFC } from "gatsby";
import Layout from "../components/layout/Layout";
import { Link, navigate } from "gatsby";

import BSCard from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Figure from "react-bootstrap/Figure";
import Button from "react-bootstrap/Button";

import Item from "../page/Item";
import Form from "react-bootstrap/Form";
import Parse, { runCloudFunction } from "../vendor/ParseApp";
import Table from "react-bootstrap/Table";
import { timeSince } from "../helpers/time";
import { handleLocation } from "../helpers/data";
import Search from "../page/Search";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";
import { graphql } from "gatsby";
import Badge from "react-bootstrap/Badge";

const handleData = (key, e) => {
  if (key === "location") {
    return handleLocation(e);
  }

  if (Object.prototype.toString.call(e) === "[object Date]") {
    return timeSince(e);
  }

  if (typeof e === "string") {
    return e;
  }

  if (Array.isArray(e)) {
    return e.map((obj) => <Badge bg="secondary"> {obj?.label} </Badge>);
  }
};

const IndexPage = () => {
  const [items, setItems] = React.useState([]);
  const { t } = useTranslation();
  React.useEffect(() => {
    runCloudFunction("searchApplication").then((items) => {
      setItems(items);
    });
  }, []);

  return (
    <Layout>
      <Search />
    </Layout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
